.hide {
  display: none; }

.formPanel .button {
  margin: 1.5rem auto 1rem;
  padding: 0; }

.formPanel .formBox {
  position: relative;
  padding: 0; }
  @media (min-width: 768px) {
    .formPanel .formBox {
      height: 100%;
      overflow-y: visible; } }
  .formPanel .formBox form.submitting {
    filter: blur(4px); }
  .formPanel .formBox .form-group {
    margin: .8rem 0 0; }
    .formPanel .formBox .form-group label {
      background-color: transparent;
      margin-left: .3rem;
      margin-bottom: -.1rem; }
    .formPanel .formBox .form-group input {
      position: relative;
      border-radius: 4px;
      border: 1px solid #ccc;
      color: currentColor;
      cursor: text;
      display: inline-flex;
      font-size: 1rem;
      box-sizing: border-box;
      align-items: center;
      font-family: sans-serif;
      font-weight: 400;
      line-height: 1.1875em;
      letter-spacing: .00938em;
      padding: .7rem 1rem;
      margin: 0;
      width: calc(100% - 2rem);
      height: 1.1875em;
      min-width: 0;
      background: #fff;
      box-sizing: content-box; }
  .formPanel .formBox .form-cta-group {
    margin: 1rem auto; }
    .formPanel .formBox .form-cta-group p {
      text-align: center;
      margin: 1rem auto;
      font-weight: 600;
      font-size: 1.3rem; }
  .formPanel .formBox h2 {
    text-align: center;
    margin: 1rem auto 2rem;
    line-height: 1.4;
    font-size: 1.7rem; }
  .formPanel .formBox .ctpaText {
    margin-top: 1rem; }
    .formPanel .formBox .ctpaText p {
      font-size: .6rem;
      line-height: 1rem; }
  .formPanel .formBox .ctaSection {
    text-align: center; }
    .formPanel .formBox .ctaSection a {
      font-size: 1.6rem;
      font-weight: 600;
      text-decoration: none; }
    .formPanel .formBox .ctaSection span {
      font-size: 1.3rem;
      font-weight: 600;
      text-decoration: none; }
  .formPanel .formBox .spacer {
    display: block;
    content: " ";
    margin-top: 0px;
    height: 0px;
    visibility: hidden;
    pointer-events: none; }
    @media (min-width: 768px) {
      .formPanel .formBox .spacer {
        height: 85px;
        margin-top: -85px; } }

.successContainer {
  background: #efefef;
  width: 100%;
  padding: 6vh 1rem; }

.form_overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: transparent;
  z-index: 1000;
  text-align: center;
  padding-top: 50%;
  display: flex;
  align-content: start;
  overflow: hidden; }
  .form_overlay .loader {
    width: 80px;
    height: 80px;
    position: relative;
    margin: 0 auto;
    display: flex;
    flex-flow: wrap row; }
    .form_overlay .loader::before {
      content: '';
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 8px solid transparent;
      border-top-color: rgba(120, 120, 120, 0.8);
      border-bottom-color: rgba(120, 120, 120, 0.8);
      -webkit-animation: spinner .8s ease infinite;
      animation: spinner .8s ease infinite;
      opacity: .8; }
    .form_overlay .loader h4:after {
      overflow: hidden;
      position: absolute;
      display: inline-block;
      vertical-align: bottom;
      -webkit-animation: ellipsis steps(4, end) 900ms infinite;
      animation: ellipsis steps(4, end) 900ms infinite;
      content: "\2026";
      /* ascii code for the ellipsis character */
      width: 0px; }

@keyframes ellipsis {
  to {
    width: 1.5rem; } }

@-webkit-keyframes ellipsis {
  to {
    width: 1.5rem; } }

@-webkit-keyframes spinner {
  to {
    transform: rotate(360deg); } }

@keyframes spinner {
  to {
    transform: rotate(360deg); } }
